import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    activeUser: null,
    genders: ["Male", "Female"],
    religions: ["Christianity", "Islam", "Traditional", "Others"],
    qualifications: ["PhD or Equivalent", "Master or Equivalent", "Bachelor or Equivalent", "HND or Equivalent", "ND or Equivalent"],
    programs: ["Website Development (Frontend)", "Website Development (Backend)", "Website Development (Fullstack)", "Mobile Application Development", "UI/UX Design and Video Animations", "Inverter and CCTV Installation"],

  },
  mutations: {
    ACTIVE_USER(state, payload) {
      state.activeUser = payload;
    },
  },
  actions: {
    setActiveUser({ commit }, payload) {
      commit("ACTIVE_USER", payload);
    },
  },
  modules: {},
});